export function highlightValidity() {
  let isFormValid = (document.querySelector('#f_store') as HTMLFormElement).checkValidity()
  if (isFormValid) return true;

  $('[required]').each(function () {
    if (!jQuery(this).val()) {
      let name = jQuery(this).attr('name')
      let parent = jQuery(`[name="${name}"]`).parent()

      jQuery(`[name="${name}"]`).addClass('invalid')
      let selectContainer = parent.find('.select2-container')
      selectContainer.addClass('invalid')
      selectContainer.on('click', function () {
        jQuery(this).removeClass('invalid')
      }).on('focus', function () {
        jQuery(this).removeClass('invalid')
      })
      jQuery(`[name="${name}"]`).on('click', function () {
        jQuery(this).removeClass('invalid')
      }).on('focus', function () {
        jQuery(this).removeClass('invalid')
      })
    }
  })
  const contacto_telefono = document.querySelector('#contacto_telefono')

  if (contacto_telefono instanceof HTMLInputElement && !/\+?56\s?9\s?[0-9]{4}\s?[0-9]{4}/.test(contacto_telefono.value)) {
    jQuery(`#contacto_telefono`)
      .addClass('invalid')
      .on('click', function () {
        jQuery(this).removeClass('invalid')
      }).on('focus', function () {
        jQuery(this).removeClass('invalid')
      })
  }
  const thisForm = document.querySelector('#f_store') as HTMLFormElement
  $(thisForm).find("button[type='submit']").prop('disabled', false);
  document.querySelector('.invalid').scrollIntoView()
  return thisForm.reportValidity();

}
