async function getMagicBytes(file: File, allowedExtensions: string[], maxSize: number = 1024 * 1024 * 10) {
  return new Promise((resolve, reject) => {
    const fileReader = new FileReader();
    fileReader.onerror = (err => reject(err))
    fileReader.onloadend = ({
      target
    }) => {
      let possibleTypes = filetype(new Uint8Array(target.result as ArrayBufferLike)).map(type => type.extension) as string[]
      //@ts-ignore
      const magic_bytes = (new Uint8Array(target.result as ArrayBufferLike)).subarray(0, 4).map(chunk => {
        return chunk.toString(16).padStart(2, '0').toUpperCase();
      });
      let validTypes = possibleTypes.find(extension => allowedExtensions.includes(extension))
      console.log({ validTypes, name: file.name, type: file.type, signature: magic_bytes.join(' '), possibleTypes });
      if (!validTypes) {
        reject(new Error(`El archivo ${file.name} es de un tipo no soportado`))
      }
      var fileSize = file.size;
      let humanFilesize = (fileSize / 1024 / 1024).toFixed(1) + 'MB'
      let humanMaxFilesize = (maxSize / 1024 / 1024).toFixed(0) + 'MB'
      if (fileSize > maxSize) {

        reject(new Error(`El tamaño máximo para subida de archivos es de ${humanMaxFilesize}. El archivo ${file.name} pesa ${humanFilesize}.`))
      }
      resolve(magic_bytes)
    };
    fileReader.readAsArrayBuffer(file);
  });

}
async function validateFileInput(event) {
  const fileElement = document.querySelector('#imagenes') as HTMLInputElement
  const allowedFileExtensions = (fileElement.dataset.allowedFileExtensions || '').split(' ')
  var fileInput = $(fileElement);
  var maxSize = Number(fileInput.data("max-file-size") || '15728640');
  // Obtener los atributos personalizados
  var maxFiles = Number(fileInput.data("max-files"));




  const dt = new DataTransfer()

  const files = fileElement.files;
  let filesLength = 0;
  //@ts-ignore
  for (let file of files) {
    await getMagicBytes(file, allowedFileExtensions, maxSize)
      .then(() => {
        dt.items.add(file);
        filesLength++
      })
      .catch(err => {

        setTimeout(() => alert(err.message), 100)
      })
  }
  // Validar el tamaño de los archivos
  if (filesLength > maxFiles) {
    alert("Solo se permiten " + maxFiles + " archivos");
  }

  fileElement.files = dt.files // Assign the updates list


}
jQuery(function () {

  const fileElement = document.querySelector('#imagenes') as HTMLInputElement
  //fileElement.addEventListener("change", validateFileInput, false);

  const token = $('#token').val();

  $("#f_store").on('submit', async function (event) {
    console.info('intercept submit')
    const isValid = highlightValidity()
    event.preventDefault();
    await validateFileInput(event)
    if (!isValid) {
      return
    }

    // prevent double submit
    $(this).find("button[type='submit']").prop('disabled', true);



    //@ts-ignore
    const formData = new FormData(this);
    // Si todos los archivos cumplen con las restricciones, continuar con la solicitud AJAX
    $.ajax({
      type: "POST",
      url: location.href,
      headers: {
        "Authorization": `Bearer ${token}`
      },
      data: formData,
      processData: false,
      enctype: 'multipart/form-data',
      contentType: false,
      success: function (data) {
        localStorage.setItem('direccion_formulario', `${$(
          '#propiedad_direccion').val()} ${$(
            '#propiedad_numero').val()}`.trim());
        submitFormEventHandler(formData);
        //@ts-ignore
        $("#f_store")[0].reset();
        $("#f_store").hide();
        $("#cuentanos").hide();
        //@ts-ignore
        location.href =
          `${location.origin}/recibimos_tu_mensaje`
      },
      error: function (data) {
        $("#f_store").find("button[type='submit']").prop(
          'disabled', false);

        // send response data to Telescope
        Sentry.captureException(data);
        console.log(data)

      }
    });
  });
});

